<template>
  <div class="d-flex flex-column col-12 m-auto">
    <div class="col-2 d-flex">
      <div
        title="Adicionar nova tarefa "
        class="ml-auto m-1 btn btn-sm btn-primary"
        @click="addNovaTarefaCliente"
        >Adicionar
      </div>
      <div
        title="Voltar"
        class="ml-auto m-1 btn btn-sm btn-secondary"
        @click="voltar"
        >Voltar
      </div>
    </div>
    <div class="card shadow">
      <div class="card-header">Lista de taréfas: {{ nomeCliente }}</div>
      <div class="card-body">
        <Suspense>
          <template #default>
            <Table
              class="table-borderless table-striped"
              :headerColumns="hColumns"
              :items="items"
              action="true"
              @on-action-click="onEdit"
            >
              <template v-slot:table-header-actions>
                <div
                  class="
                    d-flex
                    flex-center
                    justify-content-center
                    align-items-center
                  "
                >
                  Ações
                </div>
              </template>
              <template v-slot:table-actions>
                <div
                  class="
                    d-flex
                    flex-center
                    justify-content-center
                    align-items-center
                  "
                >
                  <button
                    data-edit="2"
                    class="btn btn-sm btn-primary"
                    title="Visualizar"
                  >
                    <i data-edit="2" class="fas fa-eye"></i>
                  </button>
                  <button
                    title="Editar"
                    data-edit="1"
                    class="btn btn-sm btn-warning"
                  >
                    <i data-edit="1" class="fas fa-edit"></i>
                  </button>
                  <button
                    title="Excluir"
                    data-edit="0"
                    class="btn btn-sm btn-danger"
                  >
                    <i data-edit="0" class="fas fa-trash-alt"></i>
                  </button>
                </div>
              </template>
            </Table>
          </template>
          <template #fallback> caregando tabela </template>
        </Suspense>
      </div>
    </div>
  </div>
</template>
<script>
import Table from "../../../components/Table/Table.vue";

import { ref, onMounted } from "vue";
import { useRouter, useRoute } from "vue-router";
import Swal from "sweetalert2";
import { useStore } from "vuex";
import servico from "../../../requisicoes/servicos/usuarios/index";
export default {
  setup() {
    let router = useRouter();
    let items = ref(null);
    let itemSelected = ref(null);
    let componentePaginacao = ref(null);
    let nomeCliente = ref();
    let hColumns = ref([
      {
        key: "nomeCampanha",
        label: "Campanha",
      },
    ]);

    const {
      params: { clienteId },
    } = useRoute();
    const store = useStore();
    const getId = () => {
      return clienteId ? clienteId : store.state.emptyGuid;
    };
    let idCliente = getId();

    const onEdit = (e) => {
      if (!e || !e.target || !e.target.dataset) {
        console.log("Objeto Errado");
        return;
      }
      if (e.target.dataset.edit == 1) {
        router.push(`/conjunto/tarefas-cliente/editar/${clienteId}/${e.item.id}`);
         
      }
      if (e.target.dataset.edit == 2) {
        router.push(`/conjunto/tarefas-cliente/visualizar/${clienteId}/${e.item.id}`);
      }
      if (e.target.dataset.edit == 0) {
        console.log("itemSelected", itemSelected);
        let descricaoItemSelected = e.item.value;
        Swal.fire({
          icon: "question",
          title: `Você não tem permição para excluir`,
          confirmButtonText: `Ok`,
          text: `A taréfa "${descricaoItemSelected}"`,
          // showCancelButton: true,
          // cancelButtonText: `Cancelar`,
        });
        // itemSelected = e.item.id;
        // let descricaoItemSelected = e.item.lablAtribuidaPara;
        // Swal.fire({
        //   icon: "question",
        //   title: `Deseja excluir`,
        //   confirmButtonText: `Sim`,
        //   text: `O item "${descricaoItemSelected}"?`,
        //   showCancelButton: true,
        //   cancelButtonText: `Cancelar`,
        // }).then((response) => {
        //   if (response.isConfirmed) {
        //     let dataLocalStorageTarefa = localStorage.getItem(
        //       "dataLocalStorageTarefa"
        //     );
        //     let tarefas = JSON.parse(dataLocalStorageTarefa);
        //     let itemExcluzao = itemSelected;
        //     tarefas.forEach((tarefa) => {
        //       if (tarefa.id == itemExcluzao) {
        //         let indexGrid = tarefas.indexOf(tarefa);
        //         items.value.splice(indexGrid, 1);
        //         let stringfy = JSON.stringify(items.value);
        //         localStorage.setItem("dataLocalStorageTarefa", stringfy);
        //       }
        //     });
        //     Swal.fire({
        //       icon: "success",
        //       title: `Concluído`,
        //       text: `O Item "${descricaoItemSelected}" foi excluído`,
        //     });
        //   }
        // });
      }
    };
    const addNovaTarefaCliente = () =>{    
      router.push("/conjunto/tarefas-cliente/novo/" + clienteId );
    }
    const voltar = () =>{    
      router.push("/conjuntos" );
    }

    let usuarios = [];
    let url = `conjunto/${idCliente}`;
    servico
      .get(url)
      .then((snapshot) => {
        if (snapshot.exists()) {
          let data = snapshot.val();
          nomeCliente.value = data.nomeCliente;
          if (data.tarefas) {
            console.log("data.tarefas", data.tarefas)
            for (var [key, value] of Object.entries(data.tarefas)) {
              let obj = {
                id: key, //tem que remover isso e colocar o id do banco ai fica mais rapido
                nomeCampanha: value.nomeCampanha,
              };
              usuarios.push(obj);
            }
          }
          items.value = usuarios;
        } else {
          console.log("No data available");
        }
      })
      .catch((error) => {
        console.error(error);
      });
    onMounted(async () => {
      // let dataLocalStorageTarefa = localStorage.getItem(
      //   "dataLocalStorageTarefa"
      // );
      // let tarefas = JSON.parse(dataLocalStorageTarefa);
      // items.value = tarefas;
    });
    return {
      items,
      componentePaginacao,
      hColumns,
      onEdit,
      nomeCliente,
      addNovaTarefaCliente,
      voltar
    };
  },
  components: {
    Table,
  },
};
</script>
